<template>
  <div>
    <mu-appbar style="width: 100%;" :color="$store.state.color" class="fiexdHeader">
      <div slot="left" color="#3051f1">
        <a @click="()=>{
             $router.go(-1)
          }">
          <mu-button icon size="20">
            <mu-icon color="white" value="keyboard_arrow_left"></mu-icon>
          </mu-button>
        </a>
      </div>
      <div style="width:100%;text-align:center;">
        <span style="font-size:18px;color:white;">数据列表</span>
      </div>
      <div slot="right" class="icon" size="20">
        <a>
          <!-- @click="btnAddNews()" -->
          <mu-button icon slot="left" class="icon" size="20" ref="button" @click="opened = !opened">
            <!-- <mu-icon value="add" size="24" color="white"></mu-icon> -->
            <img src="static/img/query.png" alt />
            <!-- <span style="font-size:14px;color:white" >筛选</span> -->
          </mu-button>
        </a>
      </div>
    </mu-appbar>
    <div class="dataLsitContent">
      <div>
        <div class="dataListTitle">
          <div>监测时间</div>
          <div>车辆号</div>
          <div>监测人</div>
          <div>查看详情</div>
        </div>
        <div class="dataListContent" v-for="(item,index) in dataListContent" :key="index">
          <div>{{item.name}}</div>
          <div>{{item.vehicleId}}</div>
          <div>{{item.person}}</div>
          <div @click="clickTObtn(item.routingName)">
            <a :style="'color:'+$store.state.color">查看</a>
          </div>
        </div>
      </div>
    </div>
    <mu-popover placement="bottom-end" :open.sync="open" :trigger="trigger">
      <mu-list>
        <mu-list-item button @click="btnAddNews()" v-if="showBtn">
          <mu-list-item-title>新增路线</mu-list-item-title>
        </mu-list-item>
      </mu-list>
    </mu-popover>
    <mu-container>
      <mu-bottom-sheet :open.sync="opened">
        <mu-list @item-click="closeBottomSheet">
          <mu-list-item>
            <div class="titleselect">
              <span>站点名称：</span>
            </div>
            <div class="select" @click="selectDataPicStation" id="classPicstation">
              <input type="text" placeholder="请选择站点" class="input" disabled v-model="station">
              <img src="static/img/toBottom.png" alt="" srcset="">
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>开始时间：</span>
            </div>
            <div class="select" @click="selectStartTime('start')">
              <input type="text" class="input" disabled v-model="startTime" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="titleselect">
              <span>结束时间：</span>
            </div>
            <div class="select" @click="selectLastTime('end')">
              <input type="text" class="input" disabled v-model="lastTime" />
            </div>
          </mu-list-item>
          <mu-list-item>
            <div class="btnBottom">
              <div class="left" @click="closed()">取消</div>
              <div class="right" :style="'background:'+$store.state.color" @click="confirmQuery()">确定</div>
            </div>
          </mu-list-item>
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
    <mu-container>
      <mu-bottom-sheet :open.sync="openTime">
        <mu-list @item-click="closeBottomSheetTime">
          <mu-sub-header>筛选时间</mu-sub-header>
          <van-datetime-picker
            v-model="currentDate"
            type="date"
            :min-date="minDate"
            @confirm="confirmTime"
            @cancel="close"
          />
        </mu-list>
      </mu-bottom-sheet>
    </mu-container>
  </div>
</template>
<script>
import AppBace from "../../../static/ajax";
export default {
  name: "dataListControl",
  data() {
    return {
      token: "",
      dataListContent: [],
      loading: false,
      open: false,
      trigger: null,
      opened: false,
      stationContent: [],
      station: "",
      startTime: "",
      lastTime: "",
      currentDate: new Date(),
      minDate: new Date("1970-07-01"),
      openTime: false,
      show: "",
      showBtn:false
    };
  },
  methods: {
   selectDataPicStation() {
      this.opened = false;
      var _this = this;
      var pickerView = new PickerView({
        bindElem: classPicstation, // 绑定的元素,用于区别多个组件存在时回显区别，如果单个可以随意填某个元素
        data: this.$store.state.vehicleList, // 说明：该参数必须符合json格式 且最里层是个数组，如上面的data变量所展示的[3,4]。
        title: "选择站点", // 顶部标题文本 默认为“标题”
        leftText: "取消", // 头部左侧按钮文本 默认为‘取消’
        rightText: "确定", // 头部右侧按钮文本 默认为“确定”
        rightFn: function(selectArr) {
          // 点击头部右侧按钮的回调函数，参数为一个数组，数组对应滚轮中每项对应的值
          console.log(selectArr[0]);
          _this.station = selectArr[0];
          _this.opened = true;
        }
      });
    },
  

    closed() {
      this.opened = false;
    },
    confirmTime(value) {
      // console.log(value);
      this.openTime = false;
      switch (this.show) {
        case "start":
          this.startTime = AppBace.filter(value);
          break;
        case "end":
          this.lastTime = AppBace.filter(value);
          break;
      }
    },
    closeBottomSheetTime() {
      this.openTime = false;
    },
    selectStartTime(e) {
      this.openTime = true;
      this.show = e;
    },
    selectLastTime(e) {
      this.openTime = true;
      this.show = e;
    },
    close() {
      this.openTime = false;
      this.open = false;
      // this.opened = false;
    },
    confirmQuery() {
      this.openTime = false;
      this.open = false;
      this.opened = false;
      this.initData();
    },
    openBotttomSheet() {
      this.opened = true;
      this.open = false;
    },
    closeBottomSheet() {
      this.opened = false;
    },
    clickTObtn(e) {
      this.$router.push({ path: "/addListDetails", query: { id: e } });
      sessionStorage.setItem("deviceId", e);
    },
    fullscreen() {
      this.loading = this.$loading();
    },
    async initData() {
      this.fullscreen();
      var url = "/mobile/routing";
      var pageNum = 1;
      var pageSize = 1000000;
      var startTime = AppBace.filter(this.startTime);
      var endTime = AppBace.filter(this.lastTime);
      var vehicleId = this.station;
      var res = await AppBace.request(
        url,
        "GET",
        {
          vehicleId,
          pageNum,
          pageSize,
          startTime,
          endTime
        },
        this.token
      );
      console.log("iii", res);
      if (res.data.message === "success") {
        this.loading.close();
      }
      this.dataListContent = res.data.data.rows;
    },
    btnAddNews() {
      // console.log(1221);
      this.$router.push({ path: "/addDataList" });
    }
  },
  mounted() {
    this.trigger = this.$refs.button.$el;
    this.stationContent = this.$store.state.vehicleList;
    this.startTime = AppBace.DateTime(90);
    this.lastTime = AppBace.DateTime(0);
    this.token = sessionStorage.getItem("token");
    var user = sessionStorage.getItem("user");
    switch (user) {
      case "operator":
        this.showBtn = true;
        break;
      case "admin":
        this.showBtn = true;
      default:
        break;
    }
    // setInterval(() => {
    this.initData();
    // }, 500);
  }
};
</script>
<style scoped>
@import "../../../static/css/model.css";
.dataLsitContent {
  width: 100%;
  margin-top: 56px;
  /* padding: 0 5px; */
}
.dataListTitle {
  display: flex;
  flex-direction: row;
  width: 100%;
  text-align: center;
  background: white;
  height: 40px;
  align-items: center;
  /* border-radius: 5px; */
  font-weight: 600;
  border-bottom: 1px #ccc solid;
  /* margin: 0 10px; */
}
.dataListTitle div {
  width: 25%;
  /* justify-content: center; */
}
.dataListContent {
  display: flex;
  flex-direction: row;
  width: 100%;
  text-align: center;
  background: white;
  height: 40px;
  align-items: center;
  border-radius: 5px;
  border-bottom: 1px solid rgb(235, 235, 235);
}
.dataListContent div {
  width: 25%;
  /* justify-content: center; */
}
.fiexdHeader {
  position: fixed;
  top: 0;
}
.btnBorder {
  border-bottom: 1px solid rgb(238, 238, 238);
}
.icon img{
  width: 24px;
  height: 24px;
}
</style>



